/* ngdialog
----------------------------------------------------------------------*/
body.ngdialog-open,
html.ngdialog-open {
    overflow: auto;
}

.ngdialog.ngdialog-theme-default {
    padding: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    @include lwTransition();

    ~ .ngdialog {
        margin-left: 40px;

        ~ .ngdialog {
            margin-left: 80px;
        }

        .ngdialog-overlay {
            background: rgba(0, 0, 0, 0.1);
        }
    }

    .modal-header {
        top: 0;
        z-index: 2;
        background: rgb(62, 156, 62);
        color: #fff;
        width: 100%;
        border-radius: 0;
        box-shadow: 0 0.01rem 1rem rgba(0, 0, 0, 0.15);
        height: 56px;

        h3,
        .modal-title {
            line-height: 1;
            font-size: 1.4rem;
        }
    }

    div.ngdialog-content {
        z-index: 10;
        display: block;
        background-color: white;
        margin: 0 auto;
        width: 70%;
        padding: 0;
        box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
        /*-webkit-animation: ngdialog-flyin .2s;
        animation: ngdialog-flyin 0.2s;*/

        @media (max-width: 767px) {
            width: 90%;
        }

        @media (min-width: 1200px) {
            width: 70%;
        }

        @media (min-width: 1400px) {
            width: 60%;
        }
    }

    .ngdialog-overlay {
        z-index: 1;
        // margin-right: 16px;
    }

    .ngdialog-close {
        background: transparent;
        border: none;
        z-index: 2;
        right: 6px;
        top: 6px;

        &:before,
        &:hover,
        &:active:before,
        &:hover:before {
            color: #fff;
        }

        &:before {
            position: relative;
        }
    }
}

